<template>
  <v-row class="pa-0 ma-0">
   
    <v-col cols="12"> 
      <dashboard-date-picker v-model="dates" @today="today = $event"></dashboard-date-picker>
    </v-col>
     <v-col cols="12" v-if="isAdmin" >
        <AppPendding :dates="dates" />
     </v-col>
 
    <v-col cols="12" v-if="!isWakel"> 
        <app-total :dates="dates" ></app-total> 
    </v-col>

    <v-col cols="12" v-if="!isWakel && !isAffiliate"> 
         <agent-target :dates="dates"  :today="today"></agent-target> 
    </v-col>
    <!-- Admin start -->
    <v-col cols="12" v-if="isAdmin">
      <total-sheet v-for="business in businesses" :key="business._id" :title="business.name" :id="business._id" :dates="dates"></total-sheet>
    </v-col>

    <v-col cols="12" v-if="isAdmin" >
      <v-tabs grow icons-and-text v-model="tab"  >
        <v-tab>
          Agents Transactions
          <v-icon size="20">mdi-account-cash</v-icon>
        </v-tab>
        <v-tab>
          Banks Transactions
          <v-icon size="20">mdi-bank</v-icon>
        </v-tab>
      </v-tabs>
      <v-tabs-items v-model="tab">
        <v-tab-item>
          <wakels-transactions :dates="dates" ></wakels-transactions>
        </v-tab-item>
        <v-tab-item>
          <banks-transactions :dates="dates" ></banks-transactions>
        </v-tab-item>
      </v-tabs-items>
    </v-col>
  

    <!-- Admin end -->
 
    <!-- Agent start -->
    <v-col cols="12" v-if="isAgent && isRetention" >
      <agent-upsales  :dates="dates"></agent-upsales>
    </v-col>
    <v-col cols="12" v-if="isAgent">
      <agent-report  :dates="dates"></agent-report>
    </v-col>
    <!-- Agent end -->


    <!-- Wakel start -->
    <v-col cols="12" v-if="isWakel">
       <wakel-total :dates="dates" ></wakel-total>
    </v-col>
    <!-- Wakle end -->

    <!-- Affiliate start -->
    <v-col cols="12" v-if="isAffiliate">
       <affiliate-deposits :dates="dates" ></affiliate-deposits>
    </v-col>
    
    <v-col cols="12" v-if="isAffiliate">
      <affiliate-upsales :dates="dates"  ></affiliate-upsales>
    </v-col>
    <!-- Affiliate end -->
  </v-row>
</template>

<script>
import DashboardDatePicker from './../components/dashboard/DatePicker'
import AppTotal from './../components/dashboard/Total'
import AppPendding from './../components/dashboard/Pendding'
import TotalSheet from './../components/dashboard/TotalSheet'

import AffiliateDeposits from './../components/dashboard/AffiliateDeposits'
import AffiliateUpsales from './../components/dashboard/AffiliateUpsales'



import AgentReport from './../components/dashboard/AgentReport'
import AgentUpsales from './../components/dashboard/AgentUpsales'

// import TeamLeaderReport from './../components/dashboard/TeamLeaderReport'



import WakelTotal from './../components/dashboard/WakelTotal'
import WakelsTransactions from './../components/dashboard/WakelsTransactions'



import BanksTransactions from './../components/dashboard/BanksTransactions'

import AgentTarget from '../components/dashboard/AgentTarget.vue'


import utility from '../utility'
export default {
  name: 'Dashboard',
  components:{ 
    DashboardDatePicker,
    AppTotal,
    TotalSheet,
    AffiliateDeposits,
    AffiliateUpsales,
    AgentReport,
    AgentUpsales,
    AgentTarget,
    AppPendding,
    // TeamLeaderReport,
    WakelTotal,
    WakelsTransactions,
    BanksTransactions
  },
  data: () => ({
    today: false,
    dates: utility.currentDateRange,
    businesses: [],
    tab: 0 
  }),
  computed: {
    isAdmin () {
        // is admin or owner
        return this.user.role == 'admin' || this.user.role == 'owner'
    },
    isAgent () {
      return this.user.role == 'agent'
    },
    isTeamLeader () {
      return this.user.role == 'team_leader'
    },
    isAffiliate () {
      return this.user.role == 'affiliate'
    },
    isWakel () {
      return this.user.role == 'wakel'
    },
    isSales () {
      return this.user.depositType == 'ftd'
    },
    isRetention () {
      return this.user.depositType == 'rtn'

    } 
  },
  async created () {
    if (this.isAdmin) {
      const businesses = await this.get({ collection: 'business', query: {
        sortBy: ['name'], sortDesc: [false]
      } })
      if (businesses && businesses.status == 200 && businesses.data.docs.length)
        this.businesses = businesses.data.docs
        
    }
  }
}
</script>

