<template>
  <v-main class="ma-0 pa-0 py-4">
    <v-card class="mx-auto my-2" max-width="95%">
      <v-card-title>
        Total all by currency
      </v-card-title>
      <v-card-text>
        <app-table class="my-2" :loading="loading" :headers="headers" :items="total" hide-default-footer :hide-default-header="$vuetify.breakpoint.xsOnly">
        </app-table>
      </v-card-text>
    </v-card>
    
    <v-card class="mx-auto my-2" max-width="95%" v-for="(item,i) in companies.filter(e => e.total && e.total.length)"
        :key="i" >
      <v-card-title>
        <v-icon class="mx-2">mdi-briefcase</v-icon> {{item.name}}
        <v-spacer></v-spacer>
        <v-tooltip bottom>
          <template v-slot:activator="{ attrs, on }" t>
            <v-btn icon v-bind="attrs" v-on="on" @click="showCompanyTrans(item)"><v-icon>mdi-bank-transfer</v-icon></v-btn>
          </template>
          <span>Show Company Transactions</span>
        </v-tooltip>
      </v-card-title>
      <v-card-text>
        <app-table class="my-2" :loading="loading" :headers="headers" :items="item.total" hide-default-footer :hide-default-header="$vuetify.breakpoint.xsOnly">
        </app-table>

        <v-card class="mx-auto my-2" max-width="95%" v-for="(bank,ind) in item.banks.filter(e => e.total && e.total.length)"
        :key="ind">
          <v-card-title>
             <span v-html="$options.filters.country(bank.country, true, false, 0.5)" class="mx-3"></span> {{bank.number}}
             <v-spacer></v-spacer>
            <v-tooltip bottom>
              <template v-slot:activator="{ attrs, on }" t>
                <v-btn icon v-bind="attrs" v-on="on" @click="showBankTrans(bank)"><v-icon>mdi-bank-transfer</v-icon></v-btn>
              </template>
              <span>Show Bank Transactions</span>
            </v-tooltip>
          </v-card-title>
          <v-card-text>
            <app-table class="my-2" :loading="loading" :headers="headers" :items="bank.total" hide-default-footer :hide-default-header="$vuetify.breakpoint.xsOnly">
            </app-table>
   
          </v-card-text>
        </v-card>
   
      </v-card-text>
    </v-card>
     
 
  </v-main>
</template>

<script> 
import AppTable from './../DataTable'
import collections from './../../collections'
export default {
  name: 'BanksTotalDashboard',
   components: {
    AppTable
  },
  props: {
    dates: Array
  },
  data () {
    return {
      loading: false,
      companies: [],
      total: [],
      banks: [],
      companiesBanks: {},
      banksTransactions: {}
    }
  },
  watch: {
    dates: {
      handler: 'fetchCompanies',
      immediate: true
    }
  },
   computed: {
    headers () {
       return  collections.get('banktransaction').items.total.headers 
    }
  },
  methods: {
    showBankTrans (bank) {
      const path ={ path: 'banktransaction', query: { bank: bank._id, created_on: this.dates.join('|') }}
      this.$router.push(path)
    },
    showCompanyTrans (company) {
      const path ={ path: 'banktransaction', query: { company: company._id, created_on: this.dates.join('|') }}
      this.$router.push(path)
    },
    async fetchCompanies () {
      this.total = []
      this.banks = []
      this.companies = []
      this.banksTransactions = {}
      // get totals
      this.loading = true
      const total = await this.get({ collection: 'banktransaction/total', query: {
        created_on: this.dates.join('|')
      } })
      this.total = total.data

       const companies = await this.get({ collection: 'company' })
       this.companies = companies.data.docs
       await Promise.all(this.companies.map(async (company, index) => {
        const trans =  await this.get({ collection: 'banktransaction/total', query: {
          created_on: this.dates.join('|'),
          company: company._id
        } }) 
        this.companies[index].total = trans.data

        const banks =  await this.get({ collection: 'bank', query: {
          company: company._id
        } }) 
        this.companies[index].banks = banks.data.docs
        
        await Promise.all(this.companies[index].banks.map(async (bank, ind) => {
          const transBank =  await this.get({ collection: 'banktransaction/total', query: {
            created_on: this.dates.join('|'),
             
            bank: bank._id
          } }) 

          console.log(this.companies[index].banks[ind]);
          this.companies[index].banks[ind].total = transBank.data
        }))





      }))


        this.loading = false
    }

  }
}
</script>

