<template>
    <v-expand-transition>

      <v-alert text  color="warning" v-if="!loading" >
        <v-icon>mdi-alert</v-icon>
        <span>
          There is ({{count}}) pendding deposit's 
        </span>
        <v-btn text @click="goToDeposits">Show deposits</v-btn>
      </v-alert>
    </v-expand-transition>
</template>
<script>
 

// import $ from 'jquery' 
// import dateformat from 'dateformat'
import { mapActions } from 'vuex'
export default {
  name: 'Pennding',
  props:{
    dates: Array
  },
  data () {
    return {
    
      loading: false,
      count: 0,
     
     
    }
  },
  watch: {
    dates: {
      handler: 'fetchTotals',
      immediate: true
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    goToDeposits () {
      const query = {
        status: 'pendding',
        created_on: this.dates.join('|')
      }
      this.$router.push({
        path: '/deposit',
        query
      })  
      
    }, 
    async fetchTotals () {
      this.loading = true
       
      const total = await this.get({ collection: 'dashboard/total', query: {
        created_on: this.dates.join('|'),
        status: 'pendding',
        type: 'ftd'
      } })

      if (total.status == 200) {
        const [ allTotal ] = total.data
        this.count = allTotal.count
      }


     
 
      this.loading = false
    }
  } 
 
}
</script>
