<template>
  <v-main class="ma-0 pa-0 py-4">
    <v-card class="mx-auto my-2" max-width="95%">
      <v-card-title>
        Total all transactions
      </v-card-title>
      <v-card-text>
        <app-table class="my-2" :loading="loading" :headers="headers" :items="total" hide-default-footer :hide-default-header="$vuetify.breakpoint.xsOnly">
        </app-table>
      </v-card-text>
    </v-card>
    

    <v-card   v-for="agent in agents" :key="agent._id" class="my-2 mx-auto" max-width="95%" >
      <v-card-title> 
        <v-icon>mdi-account</v-icon> {{agent.displayName}} 
        <v-spacer></v-spacer>
        <v-tooltip bottom>
          <template v-slot:activator="{ attrs, on }" t>
            <v-btn icon v-bind="attrs" v-on="on" @click="showTrans(agent)"><v-icon>mdi-bank-transfer</v-icon></v-btn>
          </template>
          <span>Show Agent Transactions</span>
        </v-tooltip>
      </v-card-title>
      <v-card-text>

        <app-table 
        class="my-2" 
        :loading="loading" 
        :headers="headers" :items="transactions[agent._id]" hide-default-footer :hide-default-header="$vuetify.breakpoint.xsOnly">
        </app-table>
       
      </v-card-text>
    </v-card>


  </v-main>
</template>
<script>
 
import AppTable from './../DataTable'
import collections from './../../collections'
export default {
  name: 'WakelsTotalDashboard',
  components: {
    AppTable
  },
  props: {
    dates: Array
  },
  data () {
    return {
      loading: false,
      total: [],
      agents: [],
      transactions: {}
    }
  },
  computed: {
    headers () {
       return  collections.get('transaction').items.total.headers 
    }
  },
  watch: {
    dates: {
      handler: 'fetchTotal',
      immediate: true
    }
  },
  methods: {
   
    showTrans (agent) {
      const path ={ path: 'transaction', query: { user: agent._id, created_on: this.dates.join('|') }}
      this.$router.push(path)
    },
    async fetchTotal () { 
      this.total = [],
      this.agents = [],
      this.transactions = {}
      // get totals
      this.loading = true
      const total = await this.get({ collection: 'transaction/total', query: {
        created_on: this.dates.join('|')
      } })
      this.total = total.data
    
     
      const agents = await this.get({ collection: 'user', query: { role: 'wakel' } })
      this.agents = agents.data.docs
      
       await Promise.all(this.agents.map(async agent => {
        const trans =  await this.get({ collection: 'transaction/total', query: {
          created_on: this.dates.join('|'),
          user: agent._id
        } })
        
        //  console.log(trans.data);
        this.transactions[agent._id] = trans.data
       
      }))
      

      this.loading = false
    }
  }
}
</script>
<style   scoped>
.fielset {
  border: 1px solid gray;
  border-radius: 5px;
}
</style>