<template>
    <v-row>
      <v-col cols="12"> <app-panel :loading="loading" title="Subtotal" currency="SAR"  :count="subtotal.count" :amount="subtotal.amount"></app-panel> </v-col>
      <v-col sm="6" cols="12"> <app-panel :loading="loading" title="Withdraw" currency="SAR" :count="withdraw.count" :amount="withdraw.amount" color="red"></app-panel> </v-col>
      <v-col sm="6" cols="12"> <app-panel :loading="loading" title="Stuck" currency="SAR" :count="stuck.count" :amount="stuck.amount" color="orange"></app-panel> </v-col>
      <v-col sm="6" cols="12"> <app-panel :loading="loading" title="Total SAR" currency="SAR" :count="totalSAR.count" :amount="totalSAR.amount" :color="getColor(totalSAR.amount)"></app-panel> </v-col>
      <v-col sm="6" cols="12"> <app-panel :loading="loading" title="Total USD" :count="totalUSD.count" :amount="totalUSD.amount"  :color="getColor(totalUSD.amount)"></app-panel> </v-col>
    </v-row>
</template>
<script>
 

// import $ from 'jquery' 
// import dateformat from 'dateformat'
import { mapActions } from 'vuex'
 import AppPanel from './Panel'
export default {
  name: 'WakelTotal',
  components: {
    AppPanel
  },
  props:{
    dates: Array
  },
  data () {
    return {
      loading: false,
      subtotal: { count: 0, amount: 0},
      withdraw: { count: 0, amount: 0},
      stuck: { count: 0, amount: 0},
      totalSAR: { count: 0, amount: 0},
      totalUSD: { count: 0, amount: 0},
    }
  },
  watch: {
    dates: {
      handler: 'fetchTotals',
      immediate: true
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    getColor (value) {
      const negative = Number(value) < 0
      return negative ? 'red' : 'green'
    },
    async fetchTotals () {
      this.loading = true
      const total = await this.get({ collection: 'transaction/total', query: {
        created_on: this.dates.join('|')
      } })
      if (total.status == 200  ) {
        const [ allTotal ] = total.data
        // console.log( total.data, allTotal);
        this.subtotal.amount = allTotal && allTotal.subtotal ? allTotal.subtotal.amount : 0
        this.subtotal.count = allTotal && allTotal.subtotal ? allTotal.subtotal.count : 0

        this.withdraw.amount = allTotal && allTotal.withdraw ? allTotal.withdraw.amount : 0
        this.withdraw.count = allTotal && allTotal.withdraw ? allTotal.withdraw.count : 0

        this.stuck.amount = allTotal && allTotal.stuck ? allTotal.stuck.amount : 0
        this.stuck.count = allTotal && allTotal.stuck ? allTotal.stuck.count : 0

        this.totalSAR.amount = allTotal && allTotal.amount ? allTotal.amount : 0
        this.totalSAR.count = allTotal && allTotal.count ? allTotal.count : 0

        this.totalUSD.amount = allTotal && allTotal.amount ? allTotal.amount / 3.9  : 0
        this.totalUSD.count = allTotal && allTotal.count ? allTotal.count   : 0
      
      }
      this.loading = false
    }
  } 
 
}
</script>
